<div class="px-4 py-5 my-5 text-center"><h1 class="display-5 fw-bold text-body-emphasis">Get & Donate Coins</h1><div class="col-lg-6 mx-auto"><p class="lead mb-4">Firstly, you must connect your wallet and switch to the <span class="tip" title="See About page">supported networks</span>.
Then, to get coins, simply click the <strong>"Get"</strong> button below.
Besides, we are really happy and thankful if you can make a donation to our decentralized funds.</p><div class="text-center"></div><template v-if="wallet.installed"><template v-if="wallet.address"><div class="alert alert-primary"><span>Your address:<br><strong>{{ obfuscatedWalletAdress }}</strong></span><br><span v-if="wallet.chain != null">{{ wallet.chain.name }}</span><span class="text-danger" v-else>Unknown or Not-supported Network</span></div><template v-if="wallet.chain != null"><div class="d-grid gap-2 d-sm-flex justify-content-sm-center"><button class="btn btn-primary btn-lg px-4 gap-3" :disabled="loading.get" @click="onGetClick" type="button">Get {{ sendingAmount }} {{ wallet.chain.nativeCurrency.symbol }}</button></div><div class="form-text" v-if="lastSentAt &gt; 0"><span :class="{'tip text-danger': lastSentMinutesAgo &lt; delayMinutes}" :title="`You can get coins once every ${delayDurationText}`"><template v-if="lastSentDuration.hour &gt; 0 || lastSentDuration.minute &gt; 0">You got coins {{ lastSentDurationText }} ago</template><template v-else>You've got coins recently</template></span></div><hr><template v-if="fund.cap &gt; 0"><div class="d-grid gap-2 d-sm-flex justify-content-sm-center"><input class="form-control border-warning text-end fw-bold" type="number" step="0.01" min="0.01" max="100" v-model="donatingAmount"><button class="btn btn-warning btn-lg px-4 gap-3" :disabled="loading.donate" @click="onDonateClick" type="button">Donate</button></div><div class="alert alert-warning small mt-2 mb-2">Click <strong>Donate</strong> to send {{ donatingAmount }} {{ wallet.chain.nativeCurrency.symbol }}
to our <span class="tip" title="Which manages the fund (see About page for the sourcecode)">smart contract</span> at the address "<strong>{{ faucetContractAddress }}</strong>" via <em>your wallet</em>.</div><div class="progress" role="progressbar" aria-label="Basic example" aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"><div class="progress-bar bg-warning" :style="{width: `${fundPercentage}%`}"></div></div><div class="small text-warning fw-bold"><span class="tip" title="Current fund">{{ Math.floor(fund.balance * 100) / 100 }} {{ wallet.chain.nativeCurrency.symbol }}</span>&nbsp;/&nbsp;<span class="tip" title="Cap">{{ Math.floor(fund.cap * 100) / 100 }} {{ wallet.chain.nativeCurrency.symbol }}</span></div></template><div class="alert alert-warning" v-else>Donation is currently disabled</div></template></template><div class="d-grid gap-2 d-sm-flex justify-content-sm-center" v-else><button class="btn btn-primary btn-lg px-4 gap-3" type="button" @click="onConnectWalletClick">Connect wallet</button></div></template><div class="alert alert-danger" v-else>Please install an EVM-compatible Wallet.</div></div></div>